import toImg from '../../components/toImg.vue'
export default{
	name:"index1",
	data(){
		return {
			banner:[],
			yuefen:{},
			vertical:[],
			canshu:{
				limit:30,
				order:"hot",
				skip:0
			},
			hasmore:true
		}
	},
	methods:{
		getindex1() {
			uni.showLoading({
				title:"加载中",
				mask:true
			})
			uni.request({
				



				

				url:"http://157.122.54.189:9088/image/v3/homepage/vertical",

				data:this.canshu,
				success: (request) => {
					//console.log(request);
					if( request.data.res.vertical.length == 0 ){
						this.hasmore = false;
						uni.showToast({
							title:"数据没有了",
							icon:"none"
							
						})							
						
						return ;
					}
					if(this.banner.length==0){
						this.banner = request.data.res.homepage[1].items;
						this.yuefen = request.data.res.homepage[2];	
						//console.log(this.banner)
					}
					
					this.vertical = [...this.vertical, ...request.data.res.vertical];
					
					
					//console.log(this.vertical);
				},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		scrolltolower() {
			if(this.hasmore){
				this.canshu.skip += this.canshu.limit;
				this.getindex1();
				//console.log(this.canshu.skip)
			}else{
				uni.showToast({
					title:"数据没有了",
					icon:"none"
					
				})
			}
			
		},

	},
	filters:{
		totime(val) {
			let date = new Date(val);
			let month = date.getMonth()+1;
			let day = date.getDate();
			return  day +"日 / " + month +"月";
		}
	},
	components:{
		toImg
	},
	mounted() {
		this.getindex1();
		
		uni.setNavigationBarTitle({
		    title:'推荐'
		});
	}
}
